@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400&display=swap);
*{box-sizing:border-box;margin:0;padding:0}h1,h2{font-family:"Playfair Display",serif;font-weight:300}p,a,small,i{font-family:"Raleway",sans-serif}a{text-decoration:none}p{font-weight:300;font-size:20px}i{font-size:16px}h1{font-size:48px}h2{font-size:36px;margin-bottom:15px}.h1-content{padding-bottom:40px;text-align:center}.btn{background-color:transparent;padding:10px 20px;border:none;text-align:center;font-size:14px;cursor:pointer;letter-spacing:2px;border-bottom:1px solid transparent;display:block;margin:30px auto 0 auto;outline:none}.btn-cv{font-weight:400;color:#000;background-color:#D5C7B9}.btn-cv:hover{color:#fff;background-color:#AC6B53}.icon-girl{width:230px;height:230px;border-radius:50%;display:flex;justify-content:center;align-items:center}.icon-girl img{width:130px;height:152px}.portfolio,.contact{padding:80px 197px}.about{padding:40px 197px}.about p{line-height:1.4}.skills-content,.works-options,.contact div,.about-content{display:flex;justify-content:space-around}.container{position:relative;display:block}.image{width:35%;top:15px;left:32%;position:absolute}footer{background-color:#5A5A5A;text-align:center;padding:5px 0;font-size:12px;color:#fff}footer a{color:#fff;text-decoration:none}@media only screen and (min-device-width: 768px) and (max-device-width: 1024px){.intro-content{padding:100px 0}.h1-intro{font-size:44px}.typewrite{font-size:36px}.mockup{left:25%}.image{left:30%}}@media only screen and (min-device-width: 0) and (max-device-width: 560px){h1{font-size:38px}h2{font-size:30px}.typewrite{font-size:28px !important}#intro,#about,#portfolio,#contact{padding:0}.about p{padding:60px 0}.intro{background-color:#32586E;background-size:cover;background-position:left;-webkit-clip-path:polygon(0px 100%, 100% 90%, 100% 0px, 0px 0px);-ms-clip-path:polygon(0px 100%, 100% 90%, 100% 0px, 0px 0px);clip-path:polygon(0px 100%, 100% 90%, 100% 0px, 0px 0px)}.intro-content{padding:280px 0;align-self:center;color:#fff}.h1-intro{font-size:38px}.typewrite{font-size:30px}#contact{padding:40px 0}#portfolio{margin:40px 0 !important}nav{display:none}.mockup{max-width:80%;left:10%}.image{width:62%;left:19%;margin-top:-5px}.image-mockup{margin-bottom:120px}.works-options,#contact div,.skills-content,.about-content{display:flex;flex-direction:column;align-items:center}#contact div{padding-top:30px}#contact p{margin-bottom:30px}}@media only screen and (min-device-width: 0) and (max-device-width: 740px){.works:first-child{margin-top:-120px !important}.works{margin-top:0 !important}.works-options{flex-direction:column}}

.portfolio{position:relative;height:580px}.mockup{max-width:45%;display:block;margin:0 auto;position:absolute;z-index:999;left:27%;margin-bottom:200px}.image-mockup{margin-bottom:260px}.works{position:relative;z-index:-999;border-bottom:1px dashed #32586E}.works:hover{border-bottom:1px solid #32586E;font-weight:800}

.skills{color:#fff;display:flex;justify-content:space-around;align-items:center;padding:140px 197px 80px 197px;-webkit-clip-path:polygon(0px 100%, 100% 100%, 100% 0px, 0px 20%);-ms-clip-path:polygon(0px 100%, 100% 100%, 100% 0px, 0px 20%);clip-path:polygon(0px 100%, 100% 100%, 100% 0px, 0px 20%);background-color:#AC6B53;line-height:1.6}.skills .skills-content{flex-direction:column}.skills .skills-itens{margin-bottom:30px}

#about p{margin-bottom:20px}#about .icon-girl{background-color:#AC6B53}#about .about-content{display:flex;flex-direction:row}#about .about-text{display:flex;flex-direction:column;width:560px}#about .box-buttons{display:flex}

nav{padding:30px 180px;display:flex;align-self:flex-end;justify-content:flex-end}.nav-item{color:#fff;font-family:'Raleway', sans-serif;text-transform:uppercase;background-color:transparent;padding:10px 20px;border:none;text-align:center;font-size:13px;cursor:pointer;letter-spacing:2px;border-bottom:1px solid transparent}.nav-item:hover{color:#a6aa9c;font-weight:700}@media only screen and (min-device-width: 0) and (max-device-width: 740px){nav{display:none}}

.intro{background-color:#32586E;-webkit-clip-path:polygon(0px 100%, 100% 80%, 100% 0px, 0px 0px);-ms-clip-path:polygon(0px 100%, 100% 80%, 100% 0px, 0px 0px);clip-path:polygon(0px 100%, 100% 80%, 100% 0px, 0px 0px);height:660px;color:#fff}.intro .intro-content{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.intro .intro-content .icon-girl{margin-left:180px;background-color:#D5C7B9}.intro .intro-text{padding:180px}.intro .typewrite::after{content:"|";margin-left:5px;opacity:1;-webkit-animation:t-writer 1s infinite;animation:t-writer 1s infinite}@-webkit-keyframes t-writer{0%,100%{opacity:1}50%{opacity:0}}@keyframes t-writer{0%,100%{opacity:1}50%{opacity:0}}

.contact{background-color:#D5C7B9;text-align:center}.contact a{display:block;color:#AC6B53;font-weight:700;text-decoration:none;font-size:16px;font-style:italic;margin-top:10px;letter-spacing:2px}.contact a:hover{color:#32586E}

